@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@500&display=swap);
body {
    background-color: #000;
    padding: 30px 50px;
    color:#fff;
    font-weight: bold;
    font-family: 'Rubik', sans-serif;}

hr {
    border-radius: 20px;
    border: 1px solid #ed1b2e;
    margin:15px 0 20px 0;}

.search_container { margin-top: 15px; }

.title{font-family: 'Rubik', sans-serif;}

.button_add {
    background-color: #ed1b2e;
    color:#fff;
    margin-left: 5px ;
    font-size: 13px;
    letter-spacing: .8px;
    cursor: pointer;
    transition: .5s;
    font-weight: bold;
    font-family: 'Rubik', sans-serif;
    padding:0 15px;}

.search_input {
    background-color: #1f1f1f;
    color: #fff;
    width: 300px;
    max-width:100%;
    font-weight: bold;
    font-family: 'Rubik', sans-serif;
    padding:10px;}

.container_categories { padding: 0 20px; }

.title {
    padding-left: 10px;
    font-size: 25px;
    margin-bottom: 10px;
}

.gif_item { padding: 0 10px 20px 10px; }

.gif_item img {border-radius: 20px;}
.category_item { margin-bottom: 10px;}

@media screen and (max-width:500px) {
    .gif_item { padding: 0 5px 10px 5px; }
    .gif_item img {border-radius: 0;}
}

@media screen and (max-width:350px) {
  body {
      padding: 10px 20px;
  }
  .container_categories {
      padding: 0 10px;
  }
  .button_add {
      font-size: 8px;
      padding: 3px 5px;
  }
  .search_input {
    padding:10px;
  }
  .gif_item { padding: 0 5px 10px 5px; }

}
* {
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
    list-style: none;
    border: none;
    outline: none;
    box-sizing: border-box;}
    
a{text-decoration: none;}
.w_100 { width: 100%;}
.h_100 { height: 100%;}
.h_100vh {height: 100vh;}
.maxW_100 {max-width: 100% !important;}

.p_relative {position: relative;}
.p_absoulte {position: absolute;}
.p_fixed {position:fixed;}

.d_grid{display:grid;}
.d_flex { display: flex; }
.d_none { display: none; }
.fx_center {justify-content: center;}
.fx_between{justify-content:space-between;}
.fx_end {justify-content: flex-end;}
.fy_center {align-items: center;}
.w_page {width:1220px;}

.c_pointer {cursor: pointer;}
.f_wrap { flex-wrap: wrap;}

.txt_center{text-align: center;}
